

import React, { Component } from "react";
import "./index.scss";
import { NavBar, Icon, Popover, ActionSheet, WingBlank, SegmentedControl } from "antd-mobile"
import { history } from "&";
import { NavLink } from "react-router-dom";
import TabsCom from "../tabs"

export const active = {
    // borderBottom:"2px solid #fff",
    // width:"-10px",
    // transition:"2s",
}
const Item = Popover.Item;
const location = window.location
const myImg = src => <img src={`https://gw.alipayobjects.com/zos/rmsportal/${src}.svg`} className="am-icon am-icon-xs" alt="" />;
export class Head extends Component {

    constructor() {
        super();
        this.state = {
            flag: true
        }
    }
    goback = show => {
        if (show) {
            history.go(-1)
        }
    }
    goSearch = () => {
        history.push("/search");
    }
    componentDidMount() {
        const hash = location.hash;
        const tab = hash.split("#/")[1];
        if (tab == "search") {
            this.setState({
                flag: false
            })
        }
    }
    render() {
        const {
            title, show, follow, titleFlag, tabs
        } = this.props;
        const {
            flag
        } = this.state;
        return (
            <div className="header">
                <NavBar
                    mode="light"
                    icon={show && <Icon type="left" />}
                    onLeftClick={() => this.goback(show)}
                    rightContent={[
                        flag && <Icon key="0" onClick={this.goSearch} type="search" style={{ marginRight: '16px' }} />,
                        <Pop key="1" />
                    ]}
                >
                    {titleFlag ? <Bookshelf title={title} follow={follow} /> : <span>{title}</span>}
                    {tabs && <TabsCom />}
                </NavBar>
            </div>
        )
    }
}
const wrapProps = {
    onTouchStart: e => e.preventDefault(),
};

const showActionSheet = () => {
    const BUTTONS = ['拍照', '从手机相册中选择', '删除', '取消'];
    ActionSheet.showActionSheetWithOptions({
        options: BUTTONS,
        cancelButtonIndex: BUTTONS.length - 1,
        destructiveButtonIndex: BUTTONS.length - 2,
        // title: 'title',
        message: '每日购',
        maskClosable: true,
        'data-seed': 'logId',
        wrapProps
    },
        (buttonIndex) => {
            console.log(buttonIndex)
        });
}
class Bookshelf extends Component {
    componentDidMount() {
        if (window.location.hash.split("#/app/")[1] == "home/money") {
            console.log("bn来了 8rem")
            this.bn.style.left = ".8rem"
        } else {
            console.log("bn来了")
            this.bn.style.left = ".15rem"
        }
    }
    componentDidUpdate() {
    }
    bottomNav = () => {
        this.bn.style.left = ".15rem"
    }
    bottomNavBack = () => {
        this.bn.style.left = ".8rem"
    }
    render() {
        const {
            title,
            follow,
        } = this.props
        return (
            <div className="topNav">
                <NavLink activeStyle={active} to="/app/home/emp" onClick={this.bottomNav}> {title} </NavLink>
                <NavLink activeStyle={active} to="/app/home/money" onClick={this.bottomNavBack}> {follow} </NavLink>
                <span className="bottom_" ref={el => this.bn = el}></span>
            </div>
        )
    }
}

class Pop extends Component {
    constructor() {
        super();
        this.state = {
            visible: false,
            selected: ""
        }
    }

    handleVisibleChange = (visible) => {
        this.setState({
            visible,
        });
    }

    onSelect = (opt) => {

        this.setState({
            visible: false,
            selected: opt.props.value,
        });
        console.log(opt.props.value);

        const value = opt.props.value;
        if (value == "scan") {
            history.push("/scan")
        } else if (value == "login") {
            history.push("/login")
        } else if (value == "photo") {
            showActionSheet();
        }

    }


    render() {
        return (
            <Popover
                mask
                overlayClassName="fortest"
                overlayStyle={{ color: 'currentColor' }}
                visible={this.state.visible}
                overlay={[
                    (<Item key="4" value="scan" icon={myImg('tOtXhkIWzwotgGSeptou')} data-seed="logId">Scan</Item>),
                    (<Item key="5" value="login" icon={myImg('PKAgAqZWJVNwKsAJSmXd')} style={{ whiteSpace: 'nowrap' }}>Login</Item>),
                    (<Item key="6" value="photo" icon={myImg('uQIYTFeRrjPELImDRrPt')}>
                        <span style={{ marginRight: 5 }}>拍照</span>
                    </Item>),
                ]}
                align={{
                    overflow: { adjustY: 0, adjustX: 0 },
                    offset: [-10, 0],
                }}
                onVisibleChange={this.handleVisibleChange}
                onSelect={this.onSelect}
            >
                <div style={{
                    height: '100%',
                    padding: '0 15px',
                    marginRight: '-15px',
                    display: 'flex',
                    alignItems: 'center',
                }}
                >
                    <Icon type="ellipsis" />
                </div>
            </Popover>
        )
    }
}