
import React, { Component } from 'react'
import { HashRouter, BrowserRouter, Switch, Route, Redirect, withRouter } from "react-router-dom";
import { Guide } from "./guide";
import { Cart } from './cart'
import { App } from "./app";
import { Home } from './home';
import  Login  from './login';
import { ReadBooks } from './readbooks'
import {SearchAll} from "./searchAll"

export class MainView extends Component {
    render() {
        return (
            <HashRouter basename="/">
                <Route component={Layout} />
            </HashRouter>
        )
    }
}

export class Layout extends Component {
    render() {
        return (
            <div id="main">
                <Switch>
                    <Route path="/guide" component={Guide} />
                    <Route path="/app/booksInfo:booksId?/readbooks" component={ReadBooks} />
                    <Route path="/app/" strict component={App}/>
                    <Route path="/login"  component={Login} />
                    <Route path="/search" component={SearchAll}/>
                    
                    <Route render={()=><Redirect to="/app/home/emp" />} />
                </Switch>
            </div>
        )
    }
}