/* eslint-disable no-undef */
import "./index.scss";
import React, { Component } from 'react'
import { Head } from "~/components/head";
import { WingBlank, WhiteSpace, Button, InputItem, List } from 'antd-mobile';
import { connect } from "react-redux";
import { axios, history } from "&"

export const mReg = /^1(3|5|7|8|9)\d{9}$/;
export const cReg = /^\d{4}$/;

let timer = null;

@connect()
class Login extends Component {
    constructor() {
        super();
        this.state = {
            codeBtnDis: true,
            loginBtnDis: true,
            txt: "获取验证码",
            count: 60,
            flag: true,
            imglog:require("@/assets/images/readLogo.jpg")
        }
    }
    checkMobile = mobile => {
        console.log("login_mobile", mobile);
        console.log(this.mobile.state.value);
        var code = this.code.state.value;
        if (this.state.flag) {
            this.setState({
                codeBtnDis: !mReg.test(mobile),
                loginBtnDis: !(mReg.test(mobile) && cReg.test(code))
            })
        }
    }
    checkCode = (code) => {
        var mobile = this.mobile.state.value;
        this.setState({
            loginBtnDis: !(mReg.test(mobile) && cReg.test(code))
        })
    }
    start = () => {
        console.log("start", this.mobile.state.value)
        axios.post("http://localhost:1903/react/sendCode",
            { mobile: this.mobile.state.value }).then(res => console.log("login_res", res));
        timer = setInterval(() => {
            if (this.state.count > 0) {
                this.setState({
                    codeBtnDis: true,
                    count: --this.state.count,
                    txt: `剩余 ${this.state.count} S`,
                    flag: false
                })
            } else {
                clearInterval(timer);
                this.setState({
                    codeBtnDis: false,
                    count: 60,
                    txt: `获取验证码`,
                    flag: true
                })
            }
        }, 1000)
    }
    loginAuto = () => {
        axios.post("http://localhost:1903/react/checkCode",
            {
                mobile: this.mobile.state.value,
                code: this.code.state.value * 1
            }).then(res => {
                console.log("res.data", res.data)
                if (res.data.type) {
                    console.log("this.props_登录成功", this.props)
                    var token = res.data.token;
                    localStorage.token = token;
                    this.props.history.push("/app/mine");
                } else {
                    localStorage.token = "";
                }
            });
    }
    render() {
        const {
            codeBtnDis,
            loginBtnDis,
            txt,
            imglog
        } = this.state;
        return (
            <div className="mydiv">
                {/* <Head title="登录" show={true}></Head> */}
                <div className="logoBgc">
                    <img src={imglog} alt="" />
                </div>
                <div className="userInfo">
                    <div className="relogo">
                        <img src="https://sta.gtimg.com/mobile/static/images/qd_icon.png" alt="" />
                    </div>
                </div>
                <WingBlank style={{ padding: "0 20px", margin: "0", backgroundColor: "#fff" }}>
                    <List style={{ width: "100%" }} className="iconfont">
                        <WhiteSpace />
                        <InputItem
                            type="tel"
                            placeholder="输入手机号快速登入并注册"
                            clear
                            ref={el => this.mobile = el}
                            onChange={this.checkMobile}
                        >&#xe6ee;</InputItem>
                        <WhiteSpace />
                        <InputItem
                            type="number"
                            placeholder="请输入您的验证码"
                            clear
                            onChange={this.checkCode}
                            ref={el => this.code = el}
                        >&#xe797;
                        <Button onClick={this.start} type="primary" size="small" disabled={codeBtnDis} inline className="l-btn"  >
                                {txt}
                            </Button>
                        </InputItem>
                        <WhiteSpace />
                        <div className="btnLogin">
                            <Button type="warning" disabled={loginBtnDis} onClick={this.loginAuto}> 马上登录/注册 </Button>
                        </div>
                    </List>
                </WingBlank>
            </div>
        )
    }
}

export default Login