import React, { Component } from "react"
import "./index.scss"
export class Dc extends Component {
    constructor() {
        super();
        this.state = {
            imgDc: require("@/assets/images/dv.jpg")
        }
    }
    render() {
        const { imgDc } = this.state
        return (
            <img src={imgDc} alt="" />
        )
    }
}