import { axios } from "&";
import React, { Component } from "react";
import { Tabs, WhiteSpace, Toast } from 'antd-mobile';
import { connect } from "react-redux";
import { HotBooks } from '~/components/hotbooks'
import { BooksInfo } from "~/components/booksInfo"



function loadingToast() {
    Toast.hide();
    Toast.loading('Loading...', 1)
    setTimeout(() => {
        Toast.hide();
    }, 3000)
}
function successToast(msg) {
    Toast.hide();
    Toast.success(msg, 1);
}
function failToast(msg) {
    Toast.hide();
    Toast.fail(msg, 1);
}
@connect(
)
class TabsCom extends Component {
    constructor() {
        super();
        this.state = {
            tabs: [
                { title: '全部热门', key: 't1' },
                { title: '玄幻精品', key: 't2' },
                { title: '古代言情', key: 't3' },
            ],
            tabs2: [
                { title: '全部热门', sub: '1' },
                { title: '玄幻精品', sub: '2' },
                { title: '古代言情', sub: '3' },
                { title: '电子体育', sub: '4' },
                { title: '王朝争霸', sub: '5' },
                { title: '高武世界', sub: '6' },
            ],
            booksInfo: [],
            hotBooks: [],
            pullNum: 4,
            all: 1,
            skip: 0,
            limit: 4
        }
    }

    componentDidMount() {
        const all = 1
        axios.get("/imgs/clc", { params: { all } }).then(res => {
            this.setState({
                booksInfo: res.data.result
            })
        })
        window.addEventListener("scroll", (e) => {
            if (document.documentElement.scrollHeight - document.documentElement.scrollTop==document.documentElement.clientHeight) {
                setTimeout(() => {
                    this.setState({
                        pullNum: this.state.pullNum + 2
                    })
                }, 300)
                loadingToast()
            }
        })
    }
    componentWillUnmount() {
        Toast.loading = () => {
        }
    }
    render() {
        const {
            tabs,
            tabs2,
            booksInfo,
            hotBooks,
            pullNum
        } = this.state
        return (
            <div>
                <WhiteSpace />
                <Tabs tabs={tabs2}
                    initialPage={0}
                    tabBarPosition="top"
                    renderTab={tab => <span>{tab.title}</span>}
                    renderTabBar={props => <Tabs.DefaultTabBar {...props} page={3} />}
                >
                    {

                        booksInfo.map((item, i) => {
                            var renderL = []
                            if (pullNum < item.booksAll.length) {
                                for (var i = 0; i < pullNum; i++) {
                                    renderL.push(item.booksAll[i])
                                }
                            } else {
                                for (var i = 0; i < item.booksAll.length; i++) {
                                    renderL.push(item.booksAll[i])
                                }
                            }
                            return (
                                <div style={{}} key={i}>
                                    <HotBooks title={item.title} bianji="24小时热销新书" imgList={item.list} onTouchmove={this.touchMove}>
                                    </HotBooks>
                                    <BooksInfo imgList={renderL} cornerAndcolor={"corner red"} />
                                </div>
                            )
                        })
                    }
                </Tabs>
                <WhiteSpace />
            </div>
        )
    }
}

export default TabsCom

