import immutable from "immutable"

const defaultState = immutable.fromJS({
    count:9999,
    city:"big wuhan",
    banner:[],
    seleTab:"home"
});


export const data = (state=defaultState,action)=>{
    switch(action.type){

        case "countAdd":
        return state.update("count",(x)=>x+action.num )
        break;

        case "getBanner":
        return state.set("banner",action.banner);
        break;

        case "changeTab":
        return state.set("seleTab",action.tab);
        break;

        default:
        return state;
        break;
    }
}