
import React, { Component } from "react"
import "./index.scss"
import {Link} from "react-router-dom"
export class BooksInfo extends Component {
    constructor() {
        super();
        this.state = {
            renderList: [],
            bookInfoNum: 0
        }
    }
    componentDidMount() {
    }
    componentDidUpdate() {
    }
    render() {
        const {
            imgList,
            cornerAndcolor,
            tabpFlag,
        } = this.props
        const {
            renderList
        } = this.state
        return (
            <div>
                <ul className="bookInfo-ul">
                    {
                        imgList.map((item, i) => {
                            return (
                                <li className="bookInfo-li" key={i}>
                                    <Link className="book-layout" to={"/app/booksInfo" + item.id}>
                                        <span className={cornerAndcolor}>
                                            <em>推荐</em>
                                        </span>
                                        <img src={item.img ? item.img : "https://bookcover.yuewen.com/qdbimg/349573/1012836003/150"} alt="" />
                                        <div className="book-cell">
                                            <h4 className="book-title">{item.title}</h4>
                                            <p className="book-desc">{item.desc}</p>
                                            <div className="book-meta">
                                                <div className="book-meta-1">
                                                    <span className="book-author iconfont">
                                                        <i className="el-icon-user">&#xe6ee;</i>
                                                        <span>{item.author}</span>
                                                    </span>
                                                </div>
                                                <div className="book-meta-2">
                                                    <span className="tag-small-group">
                                                        <em className="tag-gray tag-small">{item.noveltype}</em>
                                                        <em className="tag-red tag-small">连载中</em>
                                                        <em className="tag-bule tag-small">{item.bookNum}</em>
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul >
            </div >
        )
    }
}



