
import React, { Component } from "react";
import "./index.scss"
import { TabBar } from "antd-mobile";

// import { foots } from "../foot"
import PropTypes from "prop-types";
import { history } from "&"

const location = window.location
export class MFoot extends Component {
    constructor() {
        super();
        this.state = {
            selectedTab: "home/emp",
            foots: [
                { txt: "书架", path: "/app/home/emp", name: "home/emp", icon: 'icon-home' },
                { txt: "精选", path: "/app/classify", name: "classify", icon: "icon-fenlei" },
                { txt: "发现", path: "/app/cart", name: "cart", icon: "icon-godLike" },
                { txt: "我", path: "/app/mine", name: "mine", icon: "icon-mine" }
            ]
        }
    }

    componentDidMount() {
        const tab = location.hash.split("#/app/")[1]
        this.setState({
            selectedTab: tab
        })
        localStorage.newTab = tab;
        if (localStorage.newTab == "home/money") {
            this.setState({
                foots: [
                    { txt: "书架", path: "/app/home/money", name: "home/money", icon: 'icon-home' },
                    { txt: "精选", path: "/app/classify", name: "classify", icon: "icon-fenlei" },
                    { txt: "发现", path: "/app/cart", name: "cart", icon: "icon-godLike" },
                    { txt: "我", path: "/app/mine", name: "mine", icon: "icon-mine" }
                ]
            })
        }
    }
    render() {
        const { foots } = this.state
        return (
            <div className="footer">
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#33A3F4"
                    barTintColor="white"

                >
                    {
                        foots.map((foot, i) => {
                            return (
                                <TabBar.Item
                                    title={foot.txt}
                                    key={i}
                                    icon={<i className={"iconfont " + foot.icon} style={{
                                        width: '22px',
                                        height: '22px',
                                        display: "block",
                                    }}
                                    />
                                    }
                                    selectedIcon={<i className={"iconfont " + foot.icon} style={{
                                        width: '22px',
                                        height: '22px',
                                        display: "block",
                                        color: "red"
                                    }}
                                    />
                                    }
                                    selected={this.state.selectedTab === foot.name}
                                    badge={i == 2 ? 10 : 0}
                                    onPress={() => {
                                        // const {history} = this.context;
                                        console.log(this);
                                        history.push(foot.path);
                                        this.setState({
                                            selectedTab: foot.name,
                                        });
                                    }}
                                    data-seed="logId"
                                >
                                </TabBar.Item>
                            )
                        })
                    }

                </TabBar>
            </div>
        )
    }
}

