import React, { Component } from "react";
import "./index.scss";

export class VipList extends Component {
    render() {
        const {
            vipList,
            redColor
        } = this.props
        return (
            <ul className="vipinfo" slot="vipinfo">
                {
                    vipList.map((item, i) => {
                        return (
                            <li className="vipmod" key={i}>
                                <a className="book-layout">
                                    <div className="book-author-vv">
                                        <img src={item.authorImgurl} alt="" />
                                        <em className="tag-honor purple" style={redColor &&{backgroundColor: "#ed424b" }}>{item.level}</em>
                                    </div>
                                    <div className="book-cell">
                                        <div className="book-title-x">
                                            <h4 className="book-title">{item.author}</h4>
                                        </div>
                                        <p className="book-desc">{item.introduce}</p>
                                    </div>
                                    <i className="el-icon-arrow-right xiaoI"></i>
                                </a>
                            </li>
                        )
                    })
                }
            </ul>
        )
    }
}