
import React, { Component } from "react";


import "./index.scss";
import { ReactSwipe } from "~/components/swipe";
import {history} from "&";
const Item = ReactSwipe.Item;

export class Guide extends Component {

    constructor() {
        super();
        this.state = {
            imgs: [
                require("@/assets/images/slide1.png"),
                require("@/assets/images/slide2.png"),
                require("@/assets/images/slide3.png"),
                require("@/assets/images/slide4.png")
            ],
        }
    }

    componentDidMount() {
        if (localStorage.getItem("count")) {
            history.push("/app/home/emp");
        }
    }

    goApp = (index) => {
        const { history } = this.props;
        console.log(history)
        if (index == this.state.imgs.length - 1) {
            localStorage.setItem('count',1);
            history.push("/app/home/emp");
        }
    }

    render() {
        const {
            imgs,
        } = this.state;
        return (
            <div id="box">
                <ReactSwipe id="guide" options={{ loop: false, autoplay: 500, speed: 500 }}>
                    {
                        imgs.map((item, i) => {
                            return (
                                <Item key={i}>
                                    <img onClick={() => this.goApp(i)} src={item} alt="" className="g-img" />
                                </Item>
                            )
                        })
                    }
                </ReactSwipe>
            </div>
        )
    }
}