import React, { Component } from "react"
import "./index.scss"
import { Link } from "react-router-dom"

export class HotBooks extends Component {

    render() {
        const { bianji, title, imgList, tabpFlag, children, overflowX } = this.props
        console.log("imgList", imgList)
        return (
            <div className="books">
                <div className="hotbook">
                    <div className="booktitle">
                        <div className="bian"></div>
                        <h3>{title}</h3>
                        <span id="bianji" v-if="show">{bianji}</span>
                    </div>
                </div>
                <div id="bookInfo" style={overflowX && { overflowX: "scroll" }}>
                    {!tabpFlag && <ul className="hotbooksTop">
                        {
                            imgList.map((item, i) => {
                                return (
                                    <li key={i}>
                                        <Link to={"/app/booksInfo" + item.id}>
                                            <img src={item.img} alt="" />
                                            <h4>{item.title}</h4>
                                            <p>
                                                <span>{item.author}</span>
                                            </p>
                                        </Link>
                                    </li>
                                )
                            })
                        }
                    </ul>
                    }{children}
                </div >
            </div >
        )
    }
}