import React, { Component } from 'react'
import { history } from "&/history"
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import { BooksInfo } from "~/components/booksInfo"
import {axios} from "&"
import "./index.scss";

export class SearchAll extends Component {
    constructor() {
        super();
        this.state = {
            val: "",
            value: "卢本伟之我没开挂",
            searchBooksList:[]
        }
    }
    componentDidMount() {
        this.valInput.focus();
    }
    goback=()=>{
        history.goBack();
    }
    onChange = (value) => {
        axios.get("/imgs/imgAll",{params:{val:value}}).then(res=>{
            console.log("我他吗请求了吗")
            this.setState({
                searchBooksList:res.data.result
            })
        })
        if(!value){
            this.setState({
                searchBooksList:[]
            })
        }
    }
    render() {
        const {searchBooksList} = this.state
        return (
            <div className="iconfont">
                <WingBlank>
                    <div className="sub-title">
                        AutoFocus when enter page
                    </div>
                </WingBlank>
                <SearchBar placeholder="卢本伟重生之我没开挂" ref={el => this.valInput = el} onChange={this.onChange} onCancel={this.goback}/>
                <WhiteSpace />
                <BooksInfo imgList={searchBooksList} cornerAndcolor={"corner red"} />
            </div >
        )
    }
}