import React, { Component } from "react";
import "./index.scss";

export class FootDc extends Component {
    constructor() {
        super();
        this.state = {
            imgs: [
                require("@/assets/imgDc/dc_1.jpg"),
                require("@/assets/imgDc/dc_2.jpg"),
                require("@/assets/imgDc/dc_3.jpg"),
                require("@/assets/imgDc/dc_4.jpg")
            ]
        }
    }
    render() {
        return (
            <div className="footImg_dc">
                {
                    this.state.imgs.map((item, i) => {
                        return (
                            <img src={item} alt="" key={i} />
                        )
                    })
                }
            </div>
        )
    }
}