import { axios } from "&";

export const getBooksPush = (url, limit, skip) => {
    return axios.get(url, { params: { limit, skip } }).then(res => {
        if (skip) {
            return {
                type: "getBooksPushFourCode",
                booksListFour: res.data.result
            }
        } else {
            return {
                type: "getBooksPushCode",
                booksList: res.data.result
            }
        }
    })
}

export const getHotBooks = (url, skip, limit,girl) => {
    return axios.get(url, { params: { limit, skip } }).then(res => {
        if (girl) {
            return {
                type: "getHotBooksCode",
                hotbooksList: res.data.result
            }
        } else {
            return {
                type: "getNewBooksCode",
                newbooksList: res.data.result
            }
        }
    })
}

