export const getAllLun = (url, gender) => {
    return axios.get(url).then(res => {
        if (gender == "boy") {
            return {
                type: "getAllLunCode",
                ImgAllLun: res.data.result
            }
        } else {
            return {
                type: "getAllLunCode",
                ImgAllLun: res.data.result
            }
        }
    })
}