
import axios from "axios";

import { Toast } from "antd-mobile";

// axios.defaults.baseURL = "http://localhost:3000/" //基路径
axios.defaults.baseURL = "http://47.94.44.172:1903/" 

export function loadingToast(msg) {
    Toast.hide();
    Toast.loading(msg, 1, () => {
    });
}

export function failToast(msg) {
    Toast.hide();
    Toast.fail(msg, 1);
}

export function successToast(msg) {
    Toast.hide();
    Toast.success(msg, 1);
}

axios.interceptors.request.use(function (config) {
    console.log("config", config.url.split("/imgs/")[1])
    if (config.url.split("/imgs/")[1] == "imgAll") {
        console.log("axios-----------------进来了----imgAll")
        return config;
    } else {
        loadingToast("请求中...")
        return config;
    }
}, function (error) {
    failToast("请求失败")
    return Promise.reject(error);
});

axios.interceptors.response.use(function (response) {
    console.log(response.config.url.split("http://localhost:3000/imgs/")[1])
    if (response.config.url.split("http://localhost:3000/imgs/")[1]) {
        console.log("axios-----------------去来了----imgAll")
        return response;
    } else {
        setTimeout(() => {
            successToast(response.data.msg);
        }, 400)
        return response;
    }

}, function (error) {
    failToast("响应失败")
    return Promise.reject(error);
});


export { axios }

