
import React, { Component } from "react";
// import "./index.scss";
import { Switch, Route, Redirect } from "react-router-dom"
import Home from "../home";
import { Classify } from "../classify";
import { Cart } from "../cart";
import Mine from "../mine";
import { BooksInfo } from "../booksInfoRoute";
import { MFoot } from "~/components/mFoot";
import { ReadBooks } from '../readbooks'
import { history } from "&";
export class App extends Component {
    componentDidMount() {
        if (!localStorage.getItem("count")) {
            history.push("/guide")
        }
    }
    render() {
        return (
            <div id="box" >
                <Switch>
                    <Route path="/app/home" component={Home} />
                    <Route path="/app/classify" component={Classify} />
                    <Route path="/app/cart" component={Cart} />
                    <Route path="/app/mine" component={Mine} />
                    <Route path="/app/booksInfo:booksId?" component={BooksInfo} />
                    {/* <Route render={()=><Redirect to="/app/home"/>  }/> */}
                </Switch>
                <MFoot></MFoot>
                
            </div>
        )
    }
}