
import immutable from "immutable"

const defaultState = immutable.fromJS({
    newImgList: [],
    hotImgList: [],
    getAllLunList: [],
    newImgListTwo: [],
    newImgListfour:[],
})

export const books = (state = defaultState, action) => {
    switch (action.type) {
        case "getBooksPushCode":
            return state.set("newImgList", action.booksList);
            break;

        case "getHotBooksCode":
            return state.set("hotImgList", action.hotbooksList)
            break;

        case "getNewBooksCode":
            return state.set("newImgListTwo", action.newbooksList)
            break;

        case "getAllLunCode":
            return state.set("getAllLunList", action.ImgAllLun)
            break;

        case "getBooksPushFourCode":
            return state.set("newImgListfour",action.booksListFour)

        default:
            return state;
            break;
    }
}