
import React, { Component } from "react";
import { Head } from "~/components/head"
import { connect } from "react-redux"
import { Button } from "antd-mobile";
import { axios } from "&";
import "./index.scss";
import { getUserInfo } from "../../actions";

@connect(
    state => {
        console.log("123", state)
        return {
            isLogin: state.user.getIn(["user", "userInfo", "isLogin"]),
            mobile: state.user.getIn(["user", "userInfo", "mobile"]),
        }
    }
)
class Mine extends Component {
    constructor() {
        super();
        this.state = {
            imgLogin1: "",
            clickLogin: "点击登录",
            footLogin: "点击登录/注册"
        }
    }
    goLogin = () => {
        console.log("refs", this.refs.logAndRi.innerHTML)
        if (this.refs.login_1.innerHTML == "点击登录" || this.refs.logAndRi.innerHTML == "点击登录/注册" || this.refs.logAndRi.innerHTML == "退出登录/注销") {
            if (localStorage.token) {
                localStorage.removeItem("token");
            }
            this.props.history.push("/login");
        }
    }
    componentDidMount() {
        if (localStorage.token) {
            this.setState({
                imgLogin1: "http://img4.imgtn.bdimg.com/it/u=857259423,3357461161&fm=26&gp=0.jpg",
                clickLogin: "书友2019072652134",
                footLogin: "退出登录/注销"
            })
        } else {
            this.setState({
                imgLogin1: "https://qidian.gtimg.com/qd/images/ico/default_user.0.2.png",
                clickLogin: "点击登录",
                footLogin: "点击登录/注册"
            })
        }
    }
    render() {
        console.log("Mine_this.props", this.props);
        const {
            isLogin,
            mobile,
        } = this.props;
        const {
            imgLogin1,
            clickLogin,
            footLogin
        } = this.state
        return (
            <div>
                <Head title="个人中心" show={true}> </Head>
                <div className="userlogin iconfont">
                    <div className="page-center-user">
                        <div className="content-user">
                            <div className="center-header">
                                {/* <img src="https://qidian.gtimg.com/qd/images/ico/default_user.0.2.png" alt="" /> */}
                                <UploadImg imgLogin={imgLogin1}></UploadImg>
                                <p className="center-header-p login_4" onClick={this.goLogin} ref="login_1">{clickLogin}</p>
                            </div>
                            <div className="module-merge">
                                <a className="chongzhi">
                                    <i className="iconC iconfont">&#xe64a;</i>
                                    <p>
                                        去充值
                                <i className="el-icon-arrow iconfont" style={{ fontSize: ".23rem" }}>&#xe601;</i>
                                    </p>
                                </a>
                                <ul className="cell">
                                    <li className="center-acc-li">
                                        <span className="yuepiao">0</span>
                                        <p>月票</p>
                                    </li>
                                    <li className="center-acc-li">
                                        <span className="yuepiao">0</span>
                                        <p>推荐票</p>
                                    </li>
                                    <li className="center-acc-li">
                                        <span className="yuepiao">0</span>
                                        <p>点点币</p>
                                    </li>
                                </ul>
                            </div>
                            <div className="module-nav">
                                <ul className="center-nav-ul">
                                    <li className="book-li">
                                        <a >
                                            <h3>
                                                <i className="el-icon-bell_1 el-icon-bell" >&#xe61d;</i>
                                                消息中心
                                                <i className="el-icon-arrow-right right">&#xe601;</i>
                                            </h3>
                                            <span></span>
                                        </a>
                                    </li>
                                    <li className="book-li">
                                        <a >
                                            <h3>
                                                <i className="el-icon-bell_2 el-icon-bell" >&#xe676;</i>
                                                会员等级
                                                <i className="el-icon-arrow-right right">&#xe601;</i>
                                            </h3>
                                            <span></span>
                                        </a>
                                    </li>
                                    <li className="book-li">
                                        <a >
                                            <h3>
                                                <i className="el-icon-bell_3 el-icon-bell" >&#xe846;</i>
                                                经验等级
                                                <i className="el-icon-arrow-right right">&#xe601;</i>
                                            </h3>
                                            <span></span>
                                        </a>
                                    </li>
                                    <li className="book-li">
                                        <a >
                                            <h3>
                                                <i className="el-icon-bell_4 el-icon-bell" >&#xe669;</i>
                                                安全中心
                                                <i className="el-icon-arrow-right right">&#xe601;</i>
                                            </h3>
                                            <span></span>
                                        </a>
                                    </li>
                                    <li className="book-li">
                                        <a >
                                            <h3>
                                                <i className="el-icon-bell_5 el-icon-bell" >&#xe60d;</i>
                                                帮助中心
                                                <i className="el-icon-arrow-right right">&#xe601;</i>
                                            </h3>
                                            <span></span>
                                        </a>
                                    </li>
                                </ul>
                            </div>
                            <div className="module-login">
                                <a onClick={this.goLogin} ref="logAndRi">{footLogin}</a>
                            </div>
                        </div>
                    </div>
                </div>
                {/* <div >
                    <h2> 欢迎用户  : {mobile} </h2>
                    <UploadImg></UploadImg>
                </div> */}
            </div>

        )
    }
}
@connect(
    state => {
        return {
            userInfo: state.user.getIn(["user", "userInfo"])
        }
    }
)
class UploadImg extends Component {
    constructor() {
        super();
        this.state = {

        }
    }
    uploadFile = () => {
        console.log(this.one);
        this.one.click();
    }
    shangchuan = () => {
        const { userInfo, dispatch } = this.props;
        console.log("shangchuan_this.props", this.props)

        const data = new FormData();  // form 表单上传
        const file = this.one.files[0];  // 需要上传的文件
        data.append("avatar", file);
        console.log("data", data.get("avatar"))
        axios({
            url: "/react/uploadavatar?token=" + localStorage.token,
            method: "POST",
            data: data,
            contentType: false,
            processData: false,
        }).then(res => {
            console.log(res);
            const url = res.data.avatarUrl.replace(/static/,'');
            console.log("url",url)
            localStorage.avatar = url;
            
            this.setState({
                avatar:"http://47.94.44.172:1903"+url
            })
            // // public\avatar\1563430949200wh1.jpg 
            // // 代理 proxy
            // const url = res.data.avatarUrl.replace(/public/, '');
            // localStorage.avatar = url;
            // // info.avatar = url;
            // // var obj = immutable.Map(info);
            // userInfo.set("avatar", url);
            // dispatch(getUserInfo(userInfo));

            // this.setState({
            //     avatar: url
            // })
        })
    }

    render() {
        const {
            avatar,
        } = this.state
        const {
            imgLogin
        } = this.props
        console.log("avatar",avatar)
        return (
            <div id="photo">
                <img src={avatar||imgLogin} alt="" onClick={this.uploadFile} className="tou" />
                <input type="file" ref={el => this.one = el} accept="image/*" onChange={this.shangchuan} className="hiddenInput" />
            </div>
        )
    }
}

export default Mine