import React, { Component } from "react";
import "./index.scss";

export class ReadBooks extends Component {
    mousePull=()=>{
        console.log("点下了")
    }
    render() {
        return (
            <div className="readAll" onTouchStart={this.mousePull}>
                <div id="readCover" className="page-read-cover">
                    <div className="read-cover-v">
                        <div className="read-cover-info">
                            <figure className="read-cover-figure">
                                <img src="https://bookcover.yuewen.com/qdbimg/349573/1015090619/150" alt="" />
                            </figure>
                            <h2>穿越之富贵小锦鲤</h2>
                            <p className="auther">晶宝去旅行 著</p>
                            <ul className="read-cover-data">
                                <li>东方玄幻
                                <p>类型</p>
                                </li>
                                <li>2019.04.05上架
                                <p>上架</p>
                                </li>
                                <li>20.81万
                                <p>连载(字)</p>
                                </li>
                            </ul>
                            <ul>

                            </ul>
                        </div>
                        <div className="read-cover-copy">
                            <p className="ell">本书由潇湘网进行电子制作与发行</p>
                            <p className="read-gray">©版权所有 侵权必究</p>
                        </div>
                        <div className="rederpage">
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}