


import React, { Component } from "react";
import "@/styles/index.scss"
import { Link } from "react-router-dom"
import Swiper from 'swiper'

var mySwiper = null;


export class ReactSwipe extends Component {
    render() {
        const {
            children,
            id
        } = this.props;
        return (
            <Link to="/app/classify" className="lunImg">
                <div className="swiper-container" id={id}>
                    <div className="swiper-pagination"></div>
                    <div className="swiper-wrapper">
                        {children}
                    </div>
                </div>
            </Link>
        )
    }
    componentDidMount() {
        const {
            id,
            options, children
        } = this.props;

        if (children.length > 0) {
            mySwiper = new Swiper("#" + id, options);
        }
    }

    componentDidUpdate() {
        if (mySwiper) {
            console.log("已经销毁轮播图")
            mySwiper.destroy()
        }
        const {
            id,
            options,
            children
        } = this.props;
        if (children.length > 0) {
            mySwiper = new Swiper("#" + id, options);
        }
    }
}

// 静态属性 
ReactSwipe.Item = (props) => {
    return (
        <div className="swiper-slide">
            {props.children}
        </div>
    )
}