import React, { Component } from "react";
import "./index.scss";
import { Head } from "~/components/head"
import { Dc } from '~/components/DC'
import { FootLogin } from "~/components/footLogin"
import { Footdownload } from "~/components/footDownload"
import { Link} from "react-router-dom"
import { axios } from '&'

export class BooksInfo extends Component {
    constructor() {
        super();
        this.state = {
            oneBooksInfo: []
        }
    }
    componentDidMount() {
        console.log(this.props.match)
        var booksId = this.props.match.params.booksId
        axios.get("/imgs/clc", { params: { limit: 1, itemId: booksId } }).then(res => {
            this.setState({
                oneBooksInfo: res.data.result[0]
            })
            console.log(this.state.oneBooksInfo)
        });
    }
    render() {
        const { oneBooksInfo } = this.state
        return (
            <div className="header_info">
                <Head title={oneBooksInfo.title} show={true}></Head>
                <div className="module-merge">
                    <div className="book-detail-info">
                        <div className="book-layout">
                            <img src={oneBooksInfo.img} alt="" />
                            <div className="xiangqing">
                                <h2 className="book-title">{oneBooksInfo.title}</h2>
                                <div className="book-rand-a">
                                    <a >{oneBooksInfo.author}</a>
                                    <span className="Lv">Lv5</span>
                                </div>
                                <div className="book-score">
                                    <i className="iconfont" style={{ color:"rgb(252, 174, 6)" ,fontWeight:600}}>&#xe603;</i>
                                    <i className="iconfont" style={{ color:"rgb(252, 174, 6)" ,fontWeight:600}}>&#xe603;</i>
                                    <i className="iconfont" style={{ color:"rgb(252, 174, 6)" ,fontWeight:600}}>&#xe603;</i>
                                    <i className="iconfont" style={{ color:"rgb(252, 174, 6)" ,fontWeight:600}}>&#xe603;</i>
                                    <i className="iconfont" style={{ color:"rgb(252, 174, 6)" }}>&#xe603;</i>
                                    <span className="gray">8.8分/107人评过</span>
                                </div>
                                <p className="book-meta">{oneBooksInfo.noveltype}</p>
                                <p className="book-meta">
                                    {oneBooksInfo.bookNum}
                                    <span>|</span>
                                    连载中
                                </p>
                            </div>
                        </div>
                        <div className="book-detail-btn">
                            <ul className="btn-group">
                                <li className="btn-group-cell active">
                                    <Link to="/app/booksInfo/readbooks">免费试读</Link>
                                </li>
                                <li className="btn-group-cell">
                                    <a >加入书架</a>
                                </li>
                                <li className="btn-group-cell">VIP订阅</li>
                            </ul>
                        </div>
                        <div className="book-xiazai-btn">
                            <a >下载点点读书App</a>
                        </div>
                    </div>
                </div>
                <div className="jianJieAndmuLu">
                    <div className="jianjie">
                        <div className="content_1">{oneBooksInfo.desc}</div>
                    </div>
                    <div className="muLu">
                        <a >
                            <div className="muLu_left">目录</div>
                            <div className="muLu_right">
                                <p>12小时前.连载至936.摊上大事了?</p>
                                <i className="el-icon-arrow-right"></i>
                            </div>
                        </a>
                    </div>
                </div>
                <Dc></Dc>
                <FootLogin />
                <Footdownload />
            </div >
        )
    }
}