import React, { Component } from "react";
import "./index.scss";

export class Tabp extends Component {
    constructor() {
        super();
        this.state = {
            selected: "1",
            currIdx: 0,
            list: [
                {
                    id: 1,
                    name: "热销榜",
                    list2: [
                        { type: "悬疑" },
                        { type: "恐怖" },
                        { type: "喜剧" },
                        { type: "科幻" },
                        { type: "悬疑" },
                        { type: "恐怖" },
                        { type: "喜剧" },
                        { type: "科幻" }
                    ]
                },
                {
                    id: 2,
                    name: "风云榜",
                    list2: [
                        { type: "励志" },
                        { type: "青春" },
                        { type: "热血" },
                        { type: "科幻" },
                        { type: "科幻" }
                    ]
                },
                {
                    id: 3,
                    name: "签约榜",
                    list2: [{ type: "玄学" }, { type: "可怕" }, { type: "厉害" }]
                },
                {
                    id: 4,
                    name: "推荐榜",
                    list2: [{ type: "得到" }, { type: "啊啊" }]
                }
            ]
        }
    }
    showFlag(index) {
        this.setState({
            currIdx: this.state.currIdx == index ? this.state.currIdx : index
        })
    }
    componentDidMount(){

    }
    render() {
        const { list, selected, currIdx } = this.state
        const { daboData } = this.props
        console.log("daboData",daboData)
        return (
            <div className="navBox">
                <ul className="navAll">
                    {
                        daboData.map((item, i) => {
                            return (
                                <li className={currIdx == i ? "nav_1 active" : "nav_1"} onClick={() => this.showFlag(i)} key={i}>
                                    <span>{item.name}</span>
                                    {
                                        i == currIdx ?
                                            <ul className="nav_2" >
                                                <div className="tao">
                                                    {
                                                        item.list.map((item2, j) => {
                                                            return (
                                                                <li className="navImg" key={j}>
                                                                    <a >
                                                                        <img src={item2.img} alt="" />
                                                                        <p className="title2">{item2.title}</p>
                                                                        
                                                                            <span>{item2.author}</span>
                                                                        
                                                                    </a>
                                                                </li>
                                                            )
                                                        })
                                                    }
                                                </div>
                                            </ul>
                                            : ""
                                    }
                                </li>
                            )
                        })
                    }
                </ul>
                <div className="occupy"></div>
            </div>
        )
    }
}