
import React, { Component } from "react";
import { Head } from "~/components/head"
import { HotBooks } from '~/components/hotbooks'
import { VipList } from '~/components/vipList'
import { FootLogin } from "~/components/footLogin"
import { Footdownload } from "~/components/footDownload"
import { Dc } from '~/components/DC'
import "./index.scss";
import { axios } from '&'


export class Cart extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            vipList: [],
            godNewBooks: [],
            godList: [],
        }
    }
    componentDidMount() {
        axios.get("/imgs/clc",{params:{limit:7,skip:30}}).then(res => {
            this.setState({
                godNewBooks: res.data.result,
            })
        })
        axios.get("/imgs/goldauthor", { params: { limit: 5 } }).then(res => {
            this.setState({
                vipList: res.data.result,
            })
        })
        axios.get("/imgs/godmanAuthor").then(res => {
            this.setState({
                godList: res.data.result,
            })
        })
    }
    render() {
        const { vipList, godNewBooks, godList } = this.state
        console.log(godList)
        return (
            <div>
                <Head title="大神榜" show={true}></Head>
                <HotBooks title="大神新书" imgList={godNewBooks} overflowX={true}></HotBooks>
                <HotBooks title="白金列表" tabpFlag={true}>
                    <VipList vipList={vipList} />
                </HotBooks>
                <HotBooks title="'大神列表'" tabpFlag={true}>
                    <VipList vipList={godList} redColor={true}/>
                </HotBooks>
                <FootLogin />
                <Footdownload />
            </div>
        )
    }
}